body {
  background: #FFF;
  color: $color-text-main;
  @include f(t, 15, 21);
  @include smooth-font;
  min-width: $layout-width + 40px; }

b, strong {
  @include f(b); }

a {
  color: $color-blue;
  text-decoration: underline; }

.l {
  max-width: $layout-width + px;
  margin: auto;
  padding: 0 20px;
  box-sizing: content-box; }

.drop {
  position: relative; }
